<template lang="html">
  <section class="cards">
    <v-flex wrap row justify-center class="flex-wrap" v-if="this.currentCategorie !== 'person/popular'">
      <v-card v-for="movie in movies" width="500" height="280px" outlined @click="changeHashAndOpenAbout(movie.id, movie.name, movie.title)">
        <v-layout>
          <v-flex>
            <v-img v-if="movie.poster_path" height="278" width="185" :src="`https://image.tmdb.org/t/p/w185_and_h278_bestv2${movie.poster_path}`"></v-img>
          </v-flex>
          <v-flex>
            <v-flex row justify-start> 
              <div v-if="movie.vote_average*10 >= 0">
                <v-progress-circular v-if="movie.vote_average*10 >= 70" :rotate="-90" :size="50" :width="7" :value="movie.vote_average*10"  color="green">{{ movie.vote_average*10 }}</v-progress-circular>
                <v-progress-circular v-if="movie.vote_average*10 >= 50 && movie.vote_average*10 < 70" :rotate="-90" :size="50" :width="7" :value="movie.vote_average*10"  color="orange">{{ movie.vote_average*10 }}</v-progress-circular>
                <v-progress-circular v-if="movie.vote_average*10 < 50" :rotate="-90" :size="50" :width="7" :value="movie.vote_average*10"  color="red">{{ movie.vote_average*10 }}</v-progress-circular>
              </div>
              <div class="card-title">
                <v-card-title v-if="movie.name">{{ movie.name }}</v-card-title>
                <v-card-title v-if="movie.title">{{ movie.title }}</v-card-title>
                <v-card-text>{{ movie.release_date }}</v-card-text>
              </div>
            </v-flex>
            <v-card-text class="overview">{{ movie.overview }}</v-card-text>
            <v-card-text v-if="movie.known_for" v-for="k in movie.known_for" class="overview">{{ k.title }}</v-card-text>
          </v-flex>
        </v-layout>
      </v-card>
      <v-pagination v-model="currentPageIndex" :length="this.total_pages" class="my-10" total-visible="10"></v-pagination>
    </v-flex>


    <!-- PEOPLE -->
    <v-flex wrap row justify-center class="flex-wrap" v-if="this.currentCategorie == 'person/popular'">
      <v-card v-for="movie in movies" width="235" height="450px" outlined @click="changeHashAndOpenAbout(movie.id, movie.name)">
        <v-layout>
          <v-flex column justify-center>
            <v-img v-if="movie.profile_path" height="235" width="235" :src="`https://image.tmdb.org/t/p/w185_and_h278_bestv2${movie.profile_path}`"></v-img>
            <v-img v-if="!movie.profile_path" height="235" width="235" src="../assets/images.png"></v-img>
              <div class="card-title">
                <v-card-title v-if="movie.name">{{ movie.name }}</v-card-title>
              </div>
            <v-card-text v-if="movie.known_for" v-for="k in movie.known_for" class="overview">{{ k.title }}</v-card-text>
          </v-flex>
        </v-layout>
      </v-card>
      <v-pagination v-model="currentPageIndex" :length="this.total_pages" class="my-10" total-visible="10"></v-pagination>
    </v-flex>


  </section>
</template>

<script lang="js">
  import { mapState } from "vuex";
  export default  {
    name: 'cards',
    props: [],
    mounted() {

    },
    data() {
      return {
        page: +this.$router.history.current.path.split('=')[1],
        currentPageIndex: +this.$router.history.current.path.split('=')[1],
      }
    },
    methods: {
      changeHashAndOpenAbout(id, name, title) { 
        this.$store.dispatch('removeData');
        let movieName = '';
        if (name) {
          movieName = name.replace(/ /g, '-').toLowerCase()
        } else {
          movieName = title.replace(/ /g, '-').toLowerCase();
        }
        this.$router.push({ path: `/about/${this.currentCategorie.replace('/','-')};${movieName};${id};`}) 
      },
    },
    computed: {
      ...mapState([
        'movies',
        'total_pages',
        'currentCategorie',
        'currentPage',
      ]),
    },
    watch: {
      currentPageIndex: function (newIndex) {
        this.currentPageIndex = newIndex;
        this.$router.push({ path: `/${this.currentCategorie};page=${this.currentPageIndex}`});
        this.$store.dispatch('removeData');
        this.$store.dispatch('getJSON', {categorie: `${this.currentCategorie}`, id:'', page: `${this.$route.params.page}`});
        // this.$store.dispatch('changeCurrentPage', this.currentPageIndex);
        // console.log(this.$router.history.current.path.split('=')[1]);
      },
    },
}
</script>

