import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import { store } from './store';
import popularMovies from './components/popularMovies.vue';
import topRatedMovies from './components/topRatedMovies.vue';
import upcomingMovies from './components/upcomingMovies.vue';
import popularTVshows from './components/popularTVshows.vue';
import topRatedTVshows from './components/topRatedTVshows.vue';
import popularPeople from './components/popularPeople.vue';
import about from './components/about.vue';

// const Vue = require('vue');
import VueRouter from 'vue-router';
Vue.use(VueRouter);
Vue.config.productionTip = false

const router = new VueRouter({
  routes: [
    { path: '/movie/popular;page=:page', component: popularMovies },
    { path: '/movie/top_rated;page=:page', component: topRatedMovies },
    { path: '/movie/upcoming;page=:page', component: upcomingMovies },
    { path: '/tv/popular;page=:page', component: popularTVshows },
    { path: '/tv/top_rated;page=:page', component: topRatedTVshows },
    { path: '/person/popular;page=:page', component: popularPeople },
    { path: '/about/:categorie;:name;:id;', component: about },
  ],
  mode: 'history',
});

new Vue({
  vuetify,
  store,
  router,
  render: h => h(App)
}).$mount('#app')
